import router from './router'
import store from './store'
const whiteList = ['/login'] // 不重定向白名单

const querystring = (name) => {
    let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
    let r = (window.location.search.replace('?', '') || window.location.hash.replace('#/login?', '')).match(reg);
    if (r != null) {
        return decodeURIComponent(r[2]);
    };
    return null;
}
router.beforeEach((to, from, next) => {

    //获取token数据
    var token = localStorage.getItem('vue-token');
    console.log('match route token ' + token + " to " + to.path + " from " + from.path + " next " + next.path);

    //如果当前Hash值为login页面，则清空缓存
    var hash = window.location.hash;

    //如果当前在登录页面，则执行重新登录操作
    if (token && !hash.startsWith('#/login')) {
        if (to.path === '/login') {
            next({ path: '/conversation' })
        } else {
            next();
        }
    } else {
        if (whiteList.indexOf(to.path) !== -1) {
            next()
        } else {
            window.localStorage.clear();
            next('/login')
        }
    }
})


